<template>
  <Transition name="show">
    <div class="modal" @click="closeModal">
      <div class="modal__content" @click.stop="">
        <form class="form">
          <h3 class="add">
            {{ edit ? "Изменить заметку" : "Добавить заметку" }}
          </h3>
          <input
            type="text"
            placeholder="Title"
            class="form__input"
            v-model="title"
          />
          <input
            type="text"
            placeholder="Content"
            class="form__input"
            v-model="desc"
          />
        </form>
        <div class="modal__btns">
          <div class="btn__cansel" @click="closeModal">Отмена</div>
          <div class="btn__add" v-if="!edit" @click="addNote">Добавить</div>
          <div class="btn__add" v-else @click="changeNote">Изменить</div>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
export default {
  name: "ModalView",
  data() {
    return {
      title: "",
      desc: "",
      id: 1,
    };
  },
  props: {
    currentId: Number,
    edit: Boolean,
    editNote: Object,
  },
  methods: {
    closeModal() {
      this.$emit("closeModal", false);
      this.title = "";
      this.desc = "";
    },
    addNote() {
      if (this.title != "" && this.desc.length != "") {
        const item = {
          id: this.id++,
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", item);
        this.title = "";
        this.desc = "";
      } else {
        alert("Вы не заполнили ");
      }
    },
    changeNote() {
      if (this.title != "" && this.desc != "") {
        const editedNote = {
          id: this.editNote.id,
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("editedNote", editedNote);
        this.closeModal();
      }
    },
  },
};
</script>

