<template>
  <Header @searchValue="search = $event" />

  <Notes
    :notes="filterNotes"
    @delNote="delNote"
    @changeNote="changeNote"
    :search="search"
  />
  <AddBtn @click="openModal" />
  <Modal
    @editedNote="editedNote"
    @closeModal="closeModal"
    @addNote="addNote"
    v-show="modalWind"
    :currentId="currentId"
    :edit="edit"
    :editNote="editNote"
  />
</template>

<script>
import Header from "@/components/Header.vue";

import Notes from "@/components/Notes.vue";
import AddBtn from "@/components/AddBtn.vue";
import Modal from "@/components/Modal.vue";
export default {
  name: "HomeView",
  components: { Notes, AddBtn, Modal, Header },
  data() {
    return {
      modalWind: false,
      currentId: 1,
      notes: [
        // {id: 1, title: "Vue", desc: "Lorem ipsum dolor sit amet",  date: "07.03.2022", },
        // { id: 2, title: "React", desc: "Lorem ipsum dolor sit amet", date: "07.03.2022",},
        // { id: 3, title: "Angular",desc: "Lorem ipsum dolor sit amet",  date: "07.03.2022", },
      ],
      edit: false,
      editNote: {},
      search: "",
    };
  },
  created() {
    this.getNotes();
  },
 
  computed: {
    filterNotes() {
      return this.search
        ? this.notes.filter((note) =>
            note.title.toLowerCase().includes(this.search.toLowerCase())
          )
        : this.notes;
    },
  },
  methods: {
    openModal() {
      this.edit = false;
      this.modalWind = true;
    },
    closeModal(status) {
      this.modalWind = status;
    },
    addNote(item) {
      this.notes.push(item);
      this.modalWind = false;
    },
    delNote(id) {
      let index = this.notes.findIndex((note) => note.id == id);
      this.notes.splice(index, 1);
    },
    getNotes() {
      const localNotes = localStorage.notes;
      if (localNotes) {
        this.notes = JSON.parse(localNotes);
      }
    },
    changeNote(id) {
      this.edit = this.modalWind = true;
      let pickedNote = this.notes.find((note) => note.id == id);
      this.editNote = pickedNote;
      console.log(this.editNote);
      this.edit = true;
    },
    editedNote(noteEdited) {
      this.notes.forEach((note) => {
        if (note.id == noteEdited.id) {
          note.title = noteEdited.title;
          note.desc = noteEdited.desc;
          note.date = noteEdited.date;
        }
      });
    },
  },
  watch: {
    notes: {
      handler(/*newNotes*/) {
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true,
    },
  },
};
</script>

