<template>
  <div class="header">
    <div class="header__notes">
      <button class="header__lang" @click="changeLang()">{{ lang }}</button>
      <div class="container">
        <div class="header__change">
          <transition name="opp">
            <p class="header__p" v-if="header">Заметки</p>
            <input
              type="text"
              class="header__input"
              placeholder="Поиск..."
              v-else
              v-model="search"
            />
          </transition>
        </div>
      </div>
      <button class="header__search" @click="showSearch()">
        <div v-if="search == false">
          <i class="fal fa-search"></i>
        </div>
        <div v-else>
          <i class="far fa-times-circle"></i>
        </div>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Header-Todo",
  data() {
    return {
      lang: "ru",
      header: true,
      search: "",
    };
  },
  watch: {
    search(val) {
      this.$emit("searchValue", val);
    },
  },
  methods: {
    changeLang() {
      this.lang == "ru" ? (this.lang = "en") : (this.lang = "ru");
    },
    showSearch() {
      this.header = !this.header;
    },
  },
};
</script>

