<template>
  <div class="note__item">
    <div class="node__top">
      <h4 class="note__title">{{ note.title }}</h4>
      <p class="note__date">{{ note.date }}</p>
    </div>
    <p class="note__desc">
      {{ note.desc }}
    </p>
    <div class="note__btns">
      <button class="note__btn redact" @click="$emit('changeNote', note.id)">
        <span class="redact__icon"><i class="fas fa-pen"></i></span>
        <span class="redact__span">РЕДАКТИРОВАТЬ</span>
      </button>
      <button class="note__btn delete" @click="$emit('delNote', note.id)">
        <span class="redact__icon"><i class="fal fa-trash"></i></span>
        <span class="redact__span">Удалить</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "NoteItem",
  props: {
    note: {
      typeof: Object,
      default: {
        title: "",
        desc: "",
        date: "",
        id: "",
      },
    },
  },
  data() {
    return {};
  },
};
</script>

