<template>
  <div class="changer">
    <div class="container changer__wrap">
      <h3 class="all__notes">
        {{ notes.length > 0 ? "Все заметки" : "Нет заметок" }}
      </h3>
      <button class="ch__btn" @click="change">
        <div v-if="chang" class="ch__cont">
          <span class="btn__icon"><i class="fas fa-th-list"></i></span>
          <span class="btn__span">Список</span>
        </div>
        <div v-else class="ch__cont">
          <span class="btn__icon"><i class="fal fa-th"></i></span>
          <span class="btn__span">Сетка</span>
        </div>
      </button>
    </div>
    <div class="container">
      <div :class="[chang ? grid : flex]" v-if="notes.length > 0">
        <Note
          v-for="note in notes"
          :key="note.id"
          @changeNote="$emit('changeNote', note.id)"
          @delNote="$emit('delNote', note.id)"
          :note="note"
        />
      </div>
      <div v-else class="notesnone">Заметок нет</div>
    </div>
  </div>
</template>

<script>
import Note from "@/components/NoteItem.vue";
export default {
  name: "Content-todo",
  components: { Note },
  props: {
    notes: {
      typeof: Array,
      required: true,
    },
  },
  data() {
    return {
      flex: "changer__flex",
      grid: "changer__grid",
      chang: true,
    };
  },
  methods: {
    change() {
      this.chang = !this.chang;
    },
  },
};
</script>
